.container {
  position: relative;
  margin-top: 15px;
}
.wrapper {
  position: relative;
  margin-top: 10px;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.block {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  font-size: 70%;
}
.block span {
  font-weight: 500;
  opacity: 0.5;
}
.block h6 {
  font-weight: 500;
}