.container {
  display: flex;
  flex-direction: column;
  color: black;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
}
.container-initial {
  opacity: 1;
  visibility: visible;
}
.container h5 {
  margin-top: 25px;
  font-size: 110%;
  font-weight: 500;
}
.content-location {
  margin-top: 10px;
  font-size: 80%;
  opacity: 0.5;
}
.photo-block {
  position: relative;
  overflow: hidden;
  background: grey;
}
.back-photo {
  position: relative;
  width: 100%;
}
.main-photo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
}
.book-now {
  position: absolute;
  bottom: -60px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
}
.book-now-fon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.7;
}
.book-now-content {
  position: relative;
  color: white;
  font-weight: 500;
  font-size: 100%;
}
.container:hover .book-now {
  bottom: 0;
}