.container {
  position: relative;
  width: 100%;
  flex-grow: 1;
}
.wrapper {
  position: relative;
  margin: 0 auto;
  max-width: var(--window-max-width);
  width: var(--width-width);
  height: 100%;
  display: flex;
  flex-direction: column;
}
.title {
  position: relative;
  font-size: 150%;
  margin-bottom: 30px;
}
.cards-block {
  position: relative;
  display: grid;
  grid-column-gap: 40px;
  grid-row-gap: 70px;
  grid-template-columns: 1fr 1fr 1fr;
}