.container {
  position: relative;
  width: 100%;
  padding: 100px 0;
  overflow-x: hidden;
}
.wrapper {
  position: relative;
  max-width: var(--window-max-width);
  width: var(--width-width);
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  grid-gap: 50px;
}
.left-part {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
}
.left-part h2 {
  white-space: nowrap;
  position: relative;
  font-size: 350%;
}
.left-part p {
  white-space: nowrap;
  position: relative;
  font-style: italic;
}
.right-part-wrapper {
  position: relative;
  width: 100%;
  border: 2px solid transparent;
  padding: 40px 0 40px 30px;
  transition: 1s ease-in-out;
}
.right-part-wrapper-start-in-initial {
  animation-name: fadeIn; /* Имя ключевых кадров */
  animation-duration: 2s; /* Длительность анимации */
  animation-timing-function: ease-in-out;
  animation-delay: 1s; /* Задержка перед началом анимации */
  animation-fill-mode: forwards; /* Сохранить состояние после анимации */
}
@keyframes fadeIn {
  0% {
    border-image: linear-gradient(-90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));
    border-image-slice: 1;
  }
  25% {
    border-image: linear-gradient(-180deg, #dedede, #fff0);
    border-image-slice: 1;
  }
  50% {
    border-image: linear-gradient(-270deg, #dedede, #fff0);
    border-image-slice: 1;
  }
  75% {
    border-image: linear-gradient(0deg, #dedede, #fff0);
    border-image-slice: 1;
  }
  100% {
    border-image: linear-gradient(90deg, #dedede, #fff0);
    border-image-slice: 1;
  }
}
.right-part {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 100px;
  transition: 1s ease-in-out;
  transform: translateX(100%);
  opacity: 0;
}
.right-part-start-in-initial {
  transform: translateX(0%);
  opacity: 1;
}
.right-item-block {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 35px;
}
.right-item-block img{
  position: relative;
  border-radius: 50%;
  width: 110px;
  height: 110px;
  background: #F9E0C2;
}
.right-item-block h4 {
  font-size: 140%;
}
.right-item-block p {

}
.right-item-block button{
  position: relative;
  text-decoration: underline;
  color: #E89732;
  align-self: start;
  margin-top: auto;
  margin-bottom: 0;
}

.svg-wrapper {
  position: absolute;
  z-index: 0;
  top: -280px;
  left: 0px;
}